
<template>
  <div class="artigos-wrapper">
    <div class="artigos" v-for="p in posts" :key="p.id">
      <div class="container">
        <router-link :to="p.article_src">
          <div v-html="p.topic" class="title-topic"></div>
        </router-link>

        <div class="date-topic">
          <span v-html="p.date"></span>
        </div>
        <div v-html="p.copy" class="copy-topic"></div>
      </div>
      <router-link :to="p.article_src">
        <div class="img">
          <img :src="p.img_src" alt="" />
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import moment from "moment";

export default {
  data() {
    return {
      posts: [
        {
          id: 1,
          topic: "",
          copy: "",
          date: "",
          img_src: "",
          url_code: "",
          article_src: "",
        },
      ],
    };
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    // ***** NÃƒO ESQUECER DE LIMITAR COPY A 350 CARACTERES NO RESUMO *****

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getBlogPosts.php")
      .then((resp) => {
        const data = resp.data;
        var posts = [];

        map.call(data, function (d) {
          var src = d.img_src.split("uploads");

          const noTags = d.text.replace(/(<([^>]+)>)/gi, "");
          var length = 255;
          var trimmedString = noTags.substring(0, length);

          posts.push({
            id: d.id,
            topic: d.title,
            copy: trimmedString + "...",
            date: moment(d.date).format("DD-MM-YYYY"),
            img_src: "https://beta.anacarolinapereira.pt/uploads" + src[1],
            url_code: d.url_code,
            article_src: "/blog/" + d.url_code,
          });
        });

        vm.$data.posts = posts;
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$green: #91c569;

//paddings and margins
$pd-desktop: 0 390px;
$mg-desktop: 0 40px;

//circle
$circle: 50%;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

.artigos-wrapper {
  background-color: $white;
  height: auto;
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
    margin-top: pxToEm(50);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    margin-top: pxToEm(50);
  }

  @media only screen and (max-width: 599px) {
    padding: 0 pxToEm(20);
    margin-top: pxToEm(50);
  }

  .artigos {
    display: flex;
    height: 100%;
    justify-content: space-between;
    margin: pxToEm(150) auto;
    max-width: pxToEm(1400);
    width: 100%;
    max-height: pxToEm(300);

    @media only screen and (max-width: 767px) {
      flex-direction: column-reverse;
      align-items: center;
      max-height: unset;
    }

    .container {
      max-width: pxToEm(800);
      width: 100%;

      @media only screen and (max-width: 599px) {
        align-items: center;
        display: flex;
        flex-direction: column;

        > div {
          text-align: center;
        }
      }

      .title-topic {
        font-size: pxToEm(34);
        font-weight: bold;

        @media only screen and (max-width: 767px) {
          text-align: center;
        }
      }

      .date-topic {
        font-size: pxToEm(28);
        margin-top: pxToEm(10);

        @media only screen and (max-width: 767px) {
          text-align: center;
        }
      }

      .copy-topic {
        font-size: pxToEm(24);
        margin-top: pxToEm(20);
        text-align: justify;
        text-align-last: left;
      }
    }

    a {
      max-width: 50%;

      @media only screen and (max-width: 767px) {
        margin-bottom: pxToEm(50);
        max-height: 300px;
        max-width: 50%;
        width: 100%;
      }

      .img {
        height: 100%;
        display: flex;
        justify-content: flex-end;

        @media only screen and (max-width: 767px) {
          width: 100%;
          height: unset;
        }

        img {
          height: 100%;

          @media only screen and (max-width: 767px) {
            width: 100%;
            height: unset;
          }
        }
      }
    }
  }
}
</style>



